import axios from 'axios';

const url = 'api/drunk/';

class DataService {

    static getConfirms() {
        return new Promise ((resolve,reject) => {
            axios.get(url).then((res) => {
                resolve(res.data);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static insertConfirmation(user) {
        return new Promise ((resolve,reject) => {
            axios.post(url + "confirm", {
                "user": user, 
                "image" : "test.png"
            }).then((res) => {
                resolve(res);
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }
}

export default DataService;